import { createStore } from 'vuex';
import axios from 'axios';
// eslint-disable-next-line import/extensions,import/no-unresolved
import { CONFIG } from '@/config';

export default createStore({
  state: {
    userAccessKey: null,
    cartId: null,
    userId: null,
    productCategories: [],
    productMaterials: [],
    productCollections: [],
    filterCategory: null,
    cart: [],
  },
  getters: {
    getUserAccessKey(state) {
      if (state.userAccessKey === null) {
        state.userAccessKey = localStorage.getItem('userAccessKey');
      }

      return state.userAccessKey;
    },
    getCartId(state) {
      if (state.cartId === null) {
        state.cartId = localStorage.getItem('cartId');
      }

      return state.cartId;
    },
    getUserId(state) {
      if (state.userId === null) {
        state.userId = localStorage.getItem('userId');
      }

      return state.userId;
    },
    getProductCategories(state) {
      return state.productCategories;
    },
    getProductMaterials(state) {
      return state.productMaterials;
    },
    getProductCollections(state) {
      return state.productCollections;
    },
    getFilterCategory(state) {
      return state.filterCategory;
    },
    getCart(state) {
      if (state.cart.length === 0) {
        state.cart = localStorage.getItem('cartData') ? JSON.parse(localStorage.getItem('cartData')) : [];
      }

      return state.cart;
    },
  },
  mutations: {
    setUserAccessKey(state, key) {
      state.userAccessKey = key;
      localStorage.setItem('userAccessKey', key);
    },
    setProductCategories(state, categories) {
      state.productCategories = categories;
    },
    setProductMaterials(state, materials) {
      state.productMaterials = materials;
    },
    setProductCollections(state, collections) {
      state.productCollections = collections;
    },
    setFilterCategory(state, category) {
      state.filterCategory = category;
    },
    addCartItem(state, item) {
      state.cart.push(item);
    },
    setCart(state, cartData) {
      state.cart = cartData;
      localStorage.setItem('cartData', JSON.stringify(cartData));
    },
    setCartId(state, cartId) {
      state.cartId = cartId;
      localStorage.setItem('cartId', cartId);
    },
    setUserId(state, userId) {
      state.userId = userId;
      localStorage.setItem('userId', userId);
    },
  },
  actions: {
    async addCartItem(context, item) {
      const userKey = context.getters.getUserAccessKey;
      let query = '';
      if (userKey) {
        query = `?${(new URLSearchParams({ userAccessKey: userKey })).toString()}`;
      }
      const url = `${CONFIG.api.base_url}/api/baskets/products${query}`;

      return axios.post(url, item, {})
        .then((resp) => {
          context.commit('setCart', resp.data.items);
          // context.state.cart = resp.data.items;
          if (resp.data.id) {
            context.commit('setCartId', resp.data.id);
          }
          if (resp.data.user.id) {
            context.commit('setUserId', resp.data.user.id);
          }
          if (resp.data.user.accessKey) {
            context.commit('setUserAccessKey', resp.data.user.accessKey);
          }

          return { result: true, message: 'Товар успешно добавлен в корзину.' };
        })
        .catch(() => ({ result: false, message: 'Не удалось добавить товар в корзину.' }));
    },
  },
  modules: {
  },
});
