<template>
  <li class="catalog__item">
    <router-link
      :to="{ name: 'product', params: { id: product.id } }"
      class="catalog__pic"
    >
      <img :src="productImage" :alt="product.title">
    </router-link>

    <h3 class="catalog__title">
      <a href="#">
        {{ product.title }}
      </a>
    </h3>

    <span class="catalog__price">
      {{ product.price }} ₽
    </span>

    <ul class="colors colors--black">
      <li class="colors__item" v-for="colorItem of product.colors" :key="colorItem.id">
        <label class="colors__label">
          <input
            class="colors__radio sr-only"
            type="radio"
            :name="`color-${ product.id }`"
            :value="colorItem.color.code"
            :checked="colorItem.color.code === productColor"
            @change="changeColor(colorItem.id)"
          >
          <span class="colors__value" :style="{ 'background-color': colorItem.color.code }" />
        </label>
      </li>
    </ul>
  </li>
</template>

<script>
export default {
  name: 'CatalogItem',
  props: ['product'],
  data() {
    return {
      currentColor: null,
      currentImage: null,
    };
  },
  methods: {
    changeColor(colorId) {
      this.product.colors.forEach((item) => {
        if (item.id === colorId) {
          this.currentColor = item.color.code;
          if (item.gallery) {
            this.currentImage = item.gallery[0].file.url;
          } else {
            this.currentImage = '/img/svg/no-image.svg';
          }
        }
      });
    },
  },
  computed: {
    productColor() {
      return this.currentColor ?? this.product.colors[0].color.code;
    },
    productImage() {
      if (this.currentImage) {
        return this.currentImage;
      }

      if (this.product.colors[0].gallery && this.product.colors[0].gallery[0].file.url) {
        return this.product.colors[0].gallery[0].file.url;
      }

      return '/img/svg/no-image.svg';
    },
  },
};
</script>
