<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="productImg" width="120" height="120" :alt="productTitle">
    </div>
    <h3 class="product__title">
      {{ productTitle }}
    </h3>
    <p class="product__info product__info--color">
      Цвет:
      <span>
        <i :style="{ 'background-color': productColorCode }" />
        {{ productColorTitle }}
      </span>
    </p>
    <span class="product__code">
      Артикул: {{ productCode }}
    </span>

    <div class="product__counter form__counter">
      <button type="button" aria-label="Убрать один товар" @click="decrementItemCount">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus" />
        </svg>
      </button>

      <label>
        <input type="text" v-model="itemCount" name="count" @change="changeItemCount">
      </label>

      <button type="button" aria-label="Добавить один товар" @click="incrementItemCount">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus" />
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ productPrice }} ₽
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click="removeItem">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close" />
      </svg>
    </button>
  </li>
</template>

<script>
import axios from 'axios';
import { mapGetters, mapMutations } from 'vuex';
// eslint-disable-next-line import/extensions,import/no-unresolved
import { CONFIG } from '@/config';

export default {
  name: 'CartItem',
  props: ['item'],
  emits: ['reCalcTotalAmount'],
  data() {
    return {
      itemCount: 1,
      updateItemCountTimeout: null,
    };
  },
  computed: {
    productImg() {
      return this.item.color.gallery[0].file ? this.item.color.gallery[0].file.url : '/img/svg/no-image.svg';
    },
    productTitle() {
      return this.item.product.title;
    },
    productColorCode() {
      return this.item.color.color.code;
    },
    productColorTitle() {
      return this.item.color.color.title;
    },
    productPrice() {
      return this.itemCount * this.item.price;
    },
    productCode() {
      return this.item.product.id;
    },
  },
  methods: {
    ...mapGetters(['getUserAccessKey']),
    ...mapMutations(['setCart']),
    removeItem() {
      const userKey = this.getUserAccessKey();
      let query = '';
      if (userKey !== null) {
        query = `?${(new URLSearchParams({ userAccessKey: userKey })).toString()}`;
      }
      const url = `${CONFIG.api.base_url}/api/baskets/products${query}`;
      axios.delete(url, { data: { basketItemId: this.item.id } })
        .then((resp) => {
          this.setCart(resp.data.items);
          this.$emit('reCalcTotalAmount');
        });
    },
    changeItemCount() {
      if (this.updateItemCountTimeout) {
        clearTimeout(this.updateItemCountTimeout);
      }

      this.updateItemCountTimeout = setTimeout(() => {
        this.updateItemInCart();
        this.$emit('reCalcTotalAmount');
      }, 200);
    },
    incrementItemCount() {
      this.itemCount += 1;
      this.changeItemCount();
    },
    decrementItemCount() {
      if (this.itemCount > 1) {
        this.itemCount -= 1;
        this.changeItemCount();
      }
    },
    updateItemInCart() {
      const userKey = this.getUserAccessKey();
      let query = '';
      if (userKey !== null) {
        query = `?${(new URLSearchParams({ userAccessKey: userKey })).toString()}`;
      }
      const url = `${CONFIG.api.base_url}/api/baskets/products${query}`;
      axios.put(url, { basketItemId: `${this.item.id}`, quantity: `${this.itemCount}` })
        .then((resp) => {
          this.setCart(resp.data.items);
          this.cartData = resp.data.items;
        });
    },
  },
  created() {
    this.itemCount = this.item.quantity;
  },
};
</script>

<style scoped>
.product__del {
  cursor: pointer;
}
</style>
