<template>
  <main class="content container" v-if="isItemLoaded">

    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link
            :to="{ name: 'catalog' }"
            class="breadcrumbs__link"
          >
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link
            :to="{ name: 'catalog' }"
            class="breadcrumbs__link"
            @click="setCatalogCategory"
          >
            {{ item.category.title }}
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{ item.title }}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img
            width="570"
            height="570"
            :src="productImage"
            :alt="item.title">
        </div>
        <ul class="pics__list">
          <li class="pics__item" v-for="item of productImages" :key="item.id">
            <a
              href="#"
              class="pics__link"
              :class="{ 'pics__link--current': item.img === productImage }"
              @click.prevent="changeProductImage(item.img, item.colorId)"
            >
              <img
                width="98"
                height="98"
                :src="item.img"
                :alt="item.title">
            </a>
          </li>
        </ul>
      </div>

      <div class="item__info">
        <span class="item__code">Артикул: {{ item.id }}</span>
        <h2 class="item__title">
          {{ item.title }}
        </h2>
        <div class="item__form">
          <form class="form" action="#" @submit.prevent="addToCart">
            <div class="item__row item__row--center">
              <div class="form__counter">
                <button type="button" aria-label="Убрать один товар" @click="decrementCount">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-minus" />
                  </svg>
                </button>

                <label>
                  <input type="number" min="1" step="1" v-model="productCount" name="count" />
                </label>

                <button type="button" aria-label="Добавить один товар" @click="incrementCount">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-plus" />
                  </svg>
                </button>
              </div>

              <b class="item__price">
                {{ productPrice }} ₽
              </b>
            </div>

            <div class="item__row">
              <fieldset class="form__block">
                <legend class="form__legend">Цвет</legend>
                <ul class="colors colors--black">
                  <li class="colors__item" v-for="colorItem of item.colors" :key="colorItem.id">
                    <label class="colors__label">
                      <input
                        class="colors__radio sr-only"
                        type="radio"
                        name="color-item"
                        v-model="currentColor"
                        :checked="colorItem.color.code === productColor"
                        @change="changeColor(colorItem.id)"
                      />
                      <span class="colors__value" :style="{ 'background-color': colorItem.color.code }" />
                    </label>
                  </li>
                </ul>
              </fieldset>

              <fieldset class="form__block">
                <legend class="form__legend">Размер</legend>
                <label class="form__label form__label--small form__label--select">
                  <select class="form__select" name="category" v-model="productSize">
                    <option v-for="size of item.sizes" :key="size.id" :value="size.id">
                      {{ size.title }}
                    </option>
                  </select>
                </label>
              </fieldset>
            </div>

            <button class="item__button button button--primery" :disabled="isAddedToCart" type="submit">
              В корзину
              <span class="loader" v-if="isAddedToCart" />
            </button>

            <div class="item__row success-message" v-if="successAddedMessage !== ''">
              {{ successAddedMessage }}
            </div>

            <div class="item__row error-message" v-if="failAddedMessage !== ''">
              {{ failAddedMessage }}
            </div>

          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a
              class="tabs__link"
              href="#"
              :class="{ 'tabs__link--current': openedTab === 'about' }"
              @click.prevent="changeTabContent('about')"
            >
              Информация о товаре
            </a>
          </li>
          <li class="tabs__item">
            <a
              class="tabs__link"
              href="#"
              :class="{ 'tabs__link--current': openedTab === 'delivery' }"
              @click.prevent="changeTabContent('delivery')"
            >
              Доставка и возврат
            </a>
          </li>
        </ul>

        <div
          class="item__content tabs__content"
          :class="{ 'tabs__content--current': openedTab === 'about' }"
        >
          {{ item.content }}
        </div>

        <div
          class="item__content tabs__content"
          :class="{ 'tabs__content--current': openedTab === 'delivery' }"
        >
          <h3>Доставка:</h3>

          <p>
            1. Курьерская доставка по Москве и Московской области – 1200 ₽<br>
            2.Самовывоз из магазина. Список и адреса магазинов Вы можете посмотреть здесь<br>
          </p>

          <h3>Возврат:</h3>

          <p>
            Любой возврат должен быть осуществлен в течение Возвраты в магазине БЕСПЛАТНО.<br>
            Вы можете вернуть товары в любой магазин. Магазин должен быть расположен в стране,
            в которой Вы осуществили покупку.
            БЕСПЛАТНЫЙ возврат в Пункт выдачи заказов.<br>
            Для того чтобы вернуть товар в одном из наших Пунктов выдачи заказов, позвоните по
            телефону 8 800 600 90 09<br>
          </p>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
// eslint-disable-next-line import/no-unresolved
import axios from 'axios';
// eslint-disable-next-line import/extensions,import/no-unresolved
import { mapActions, mapMutations } from 'vuex';
// eslint-disable-next-line import/extensions,import/no-unresolved
import { CONFIG } from '@/config';

export default {
  name: 'ProductPage',
  data() {
    return {
      item: null,
      loadingProduct: true,
      failLoadingProduct: false,
      failLoadingProductError: '',
      currentImage: null,
      currentColor: null,
      productCount: 1,
      productSize: null,
      productColorId: null,
      isAddedToCart: false,
      successAddedMessage: '',
      failAddedMessage: '',
      currentTab: 'about',
    };
  },
  methods: {
    ...mapMutations(['setFilterCategory']),
    ...mapActions(['addCartItem']),
    loadItem() {
      const url = `${CONFIG.api.base_url}/api/products/${this.$route.params.id}`;
      axios.get(url)
        .then((resp) => {
          this.item = resp.data;
          this.loadingProduct = false;
          this.productSize = this.item.sizes[0].id;
          this.productColorId = this.item.colors[0].color.id;
        })
        .catch(() => {});
    },
    changeProductImage(img, colorId) {
      this.currentImage = img;
      this.currentColor = colorId;
      this.item.colors.forEach((item) => {
        if (item.color.code === colorId) {
          this.productColorId = item.color.id;
        }
      });
    },
    changeColor(colorId) {
      this.item.colors.forEach((item) => {
        if (item.id === colorId) {
          this.productColorId = item.color.id;
          if (item.gallery && item.gallery[0].file) {
            this.currentImage = item.gallery[0].file.url;
          } else {
            this.currentImage = null;
          }
        }
      });
    },
    addToCart() {
      this.isAddedToCart = true;
      const item = {
        productId: this.item.id,
        colorId: this.productColorId,
        sizeId: this.productSize,
        quantity: this.productCount,
      };

      const res = this.addCartItem(item);
      res.then((resp) => {
        if (resp.result === true) {
          this.successAddedMessage = resp.message;
          setTimeout(() => { this.successAddedMessage = ''; }, 3000);
        } else {
          this.failAddedMessage = resp.message;
          setTimeout(() => { this.failAddedMessage = ''; }, 3000);
        }

        this.isAddedToCart = false;
      });
    },
    incrementCount() {
      this.productCount += 1;
    },
    decrementCount() {
      if (this.productCount > 1) {
        this.productCount -= 1;
      }
    },
    setCatalogCategory() {
      this.setFilterCategory(this.item.category.id);
    },
    changeTabContent(tabName) {
      this.currentTab = tabName;
    },
  },
  computed: {
    isItemLoaded() {
      return this.item !== null
        && this.loadingProduct === false
        && this.failLoadingProduct === false;
    },
    productImage() {
      if (this.currentImage) {
        return this.currentImage;
      }

      if (this.item.colors[0].gallery && this.item.colors[0].gallery[0].file.url) {
        return this.item.colors[0].gallery[0].file.url;
      }

      return '/img/svg/no-image.svg';
    },
    productColor() {
      return this.currentColor ?? this.item.colors[0].color.code;
    },
    productImages() {
      const res = [];
      this.item.colors.forEach((item) => {
        if (item.gallery) {
          item.gallery.forEach((img) => {
            if (img.file && img.file.url) {
              res.push({
                img: img.file.url,
                colorId: item.color.code,
                current: img.file.url === this.currentImage,
              });
            }
          });
        }
      });

      return res;
    },
    productPrice() {
      return this.productCount * this.item.price;
    },
    openedTab() {
      return this.currentTab;
    },
  },
  mounted() {
    this.loadItem();
  },
};
</script>

