<template>
  <main class="content container">
    <div class="content__top">

      <div class="content__row">
        <h1 class="content__title">
          Каталог
        </h1>
        <span class="content__info">
          {{ totalItems }} товара
        </span>
      </div>
    </div>

    <div class="content__catalog">

      <Filter
        :product-categories="productCategories"
        :product-materials="productMaterials"
        :product-collections="productCollections"
        :filter-materials="filterMaterial"
        :filter-category="filterCategory"
        :filter-collections="filterCollection"
        :show-reset="showReset"
        @reload-product-list="loadFilteredProducts"
      />

      <section class="catalog">
        <Loader v-if="loadingProducts" />

        <CatalogList
          v-if="!loadingProducts && !failLoadingProducts"
          :loading-products="loadingProducts"
          :products="products"
        />

        <div class="content container fail-loading" v-if="!loadingProducts && failLoadingProducts">
          Не удалось загрузить данные заказа.
        </div>

        <Pagination
          :page="currentPage"
          :pages="totalPages"
          :total="totalItems"
          @change-page="changePage"
        />
      </section>
    </div>
  </main>
</template>

<script>
// eslint-disable-next-line import/no-unresolved
import axios from 'axios';
// eslint-disable-next-line import/no-unresolved
import { mapGetters, mapMutations } from 'vuex';
// eslint-disable-next-line import/no-unresolved
import Filter from '@/components/Filter.vue';
// eslint-disable-next-line import/no-unresolved
import CatalogList from '@/components/CatalogList.vue';
// eslint-disable-next-line import/no-unresolved
import Pagination from '@/components/Pagination.vue';
// eslint-disable-next-line import/extensions,import/no-unresolved
import { CONFIG } from '@/config';
// eslint-disable-next-line no-unused-vars,import/no-unresolved
import Loader from '@/components/Loader.vue';

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'Catalog',
  // eslint-disable-next-line vue/no-reserved-component-names
  components: {
    // eslint-disable-next-line vue/no-reserved-component-names
    Pagination, CatalogList, Filter, Loader,
  },
  data() {
    return {
      products: [],
      loadingProducts: true,
      failLoadingProducts: false,
      failLoadingProductError: '',
      filterMaxPrice: 0,
      filterMinPrice: 0,
      filterCategory: 0,
      filterMaterial: [],
      filterCollection: [],
      page: 1,
      pageLimit: 12,
      pages: 1,
      total: 0,
    };
  },
  props: ['categoryId'],
  methods: {
    ...mapGetters([
      'getUserAccessKey',
      'getProductCategories',
      'getProductMaterials',
      'getProductCollections',
      'getFilterCategory',
    ]),
    ...mapMutations([
      'setProductCategories',
      'setProductMaterials',
      'setProductCollections',
      'setFilterCategory',
    ]),
    loadProducts() {
      this.loadingProducts = true;
      const url = `${CONFIG.api.base_url}/api/products/?${this.filterParams}`;
      axios.get(url)
        .then((resp) => {
          this.products = resp.data.items;
          this.pages = resp.data.pagination.pages;
          this.total = resp.data.pagination.total;
          this.loadingProducts = false;
        })
        .catch(() => {
          this.loadingProducts = false;
          this.failLoadingProducts = true;
          this.failLoadingProductError = 'Не удалось загрузить список товаров. Попробуйте немного позже...';
        });
    },
    loadCategory() {
      if (this.getProductCategories.length > 0) {
        return;
      }

      const url = `${CONFIG.api.base_url}/api/productCategories`;
      axios.get(url)
        .then((resp) => {
          this.setProductCategories(resp.data.items);
        })
        .catch();
    },
    loadMaterial() {
      if (this.getProductMaterials().length > 0) {
        return;
      }

      const url = `${CONFIG.api.base_url}/api/materials`;
      axios.get(url)
        .then((resp) => {
          this.setProductMaterials(resp.data.items);
        })
        .catch();
    },
    loadCollection() {
      if (this.getProductCollections().length > 0) {
        return;
      }

      const url = `${CONFIG.api.base_url}/api/seasons`;
      axios.get(url)
        .then((resp) => {
          this.setProductCollections(resp.data.items);
        })
        .catch();
    },
    loadFilteredProducts(props) {
      this.filterCategory = props.filterCategory;
      this.filterCollection = props.filterCollection;
      this.filterMaterial = props.filterMaterials;
      this.filterMaxPrice = props.filterMaxPrice;
      this.filterMinPrice = props.filterMinPrice;
      this.loadProducts();
    },
    changePage(event) {
      this.page = event;
      this.loadProducts();
    },
  },
  computed: {
    showReset() {
      return this.filterMaxPrice !== 0
        || this.filterMinPrice !== 0
        || this.filterCategory !== 0
        || this.filterMaterial.length !== 0
        || this.filterCollection.length !== 0;
    },
    filterParams() {
      const query = {};
      if (this.filterCategory > 0) {
        query.categoryId = this.filterCategory;
      }

      if (this.filterMaterial.length > 0) {
        query['materialIds[]'] = this.filterMaterial;
      }

      if (this.filterCollection.length > 0) {
        query['seasonIds[]'] = this.filterCollection;
      }

      if (this.filterMinPrice > 0) {
        query.minPrice = this.filterMinPrice;
      }

      if (this.filterMaxPrice > 0) {
        query.maxPrice = this.filterMaxPrice;
      }

      query.page = this.currentPage;
      query.limit = this.pageLimit;

      return (new URLSearchParams(query)).toString();
    },
    currentPage() {
      return this.page;
    },
    totalPages() {
      return this.pages;
    },
    totalItems() {
      return this.total;
    },
    productCategories() {
      return this.getProductCategories();
    },
    productMaterials() {
      return this.getProductMaterials();
    },
    productCollections() {
      return this.getProductCollections();
    },
  },
  created() {
    this.filterCategory = this.getFilterCategory() ?? 0;
    this.setFilterCategory(null);
  },
  mounted() {
    this.loadCategory();
    this.loadCollection();
    this.loadMaterial();
    this.loadProducts();
  },
};
</script>

<style scoped>
.fail-loading {
  text-align: center;
  color: red;
  font-weight: bold;
  font-size: 18px;
}
</style>
