<template>
  <div>
    <div class="content container loader-block" v-if="isLoadingOrder">
      <div class="loader-content">
        <div class="loader" />
      </div>
    </div>
    <div class="content container fail-load-order" v-if="!isLoadingOrder && failLoadingOrder">
      Не удалось загрузить данные заказа.
    </div>
    <main class="content container" v-if="!isLoadingOrder && !failLoadingOrder">
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <router-link :to="{ name: 'catalog' }" class="breadcrumbs__link">
              Каталог
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <router-link :to="{ name: 'cart' }" class="breadcrumbs__link">
              Корзина
            </router-link>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link">
              Оформление заказа
            </a>
          </li>
        </ul>

        <h1 class="content__title">
          Заказ оформлен <span>№ {{ order.id }}</span>
        </h1>
      </div>

      <section class="cart">
        <form class="cart__form form" action="#" method="POST">
          <div class="cart__field">
            <p class="cart__message">
              Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо
              с&nbsp;деталями заказа. Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа
              для уточнения деталей доставки.
            </p>

            <ul class="dictionary">
              <li class="dictionary__item">
                <span class="dictionary__key">
                  Получатель
                </span>
                <span class="dictionary__value">
                  {{ order.name }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key">
                  Адрес доставки
                </span>
                <span class="dictionary__value">
                  {{ order.address }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key">
                  Телефон
                </span>
                <span class="dictionary__value">
                  {{ order.phone }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key">
                  Email
                </span>
                <span class="dictionary__value">
                  {{ order.email }}
                </span>
              </li>
              <li class="dictionary__item">
                <span class="dictionary__key">
                  Способ оплаты
                </span>
                <span class="dictionary__value">
                  {{ order.paymentType }}
                </span>
              </li>
            </ul>
          </div>

          <div class="cart__block">
            <ul class="cart__orders">
              <li class="cart__order" v-for="item of order.basket.items" :key="item.id">
                <h3>{{ item.product.title }}</h3>
                <b>{{ item.quantity * item.price }} ₽</b>
                <span>Артикул: {{ item.product.id }}</span>
              </li>
            </ul>

            <div class="cart__total">
              <p>Доставка: <b>{{ parseInt(order.deliveryType.price) === 0 ? 'бесплатно' : `${order.deliveryType.price} ₽`}}</b></p>
              <p>
                Итого: <b>{{ order.basket.items.length }}</b>
                товара на сумму <b>{{ order.totalPrice }} ₽</b>
              </p>
            </div>
          </div>
        </form>
      </section>
    </main>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import axios from 'axios';
// eslint-disable-next-line import/extensions,import/no-unresolved
import { CONFIG } from '@/config';

export default {
  name: 'OrderInfoPage',
  data() {
    return {
      order: null,
      isLoadingOrder: true,
      failLoadingOrder: false,
      failLoadingOrderMessage: '',
    };
  },
  methods: {
    ...mapGetters(['getUserAccessKey']),
    loadOrderData() {
      // 3599
      const userKey = this.getUserAccessKey();
      let query = '';
      if (userKey !== null) {
        query = `?${(new URLSearchParams({ userAccessKey: userKey })).toString()}`;
      }
      const url = `${CONFIG.api.base_url}/api/orders/${this.$route.params.id}${query}`;
      axios.get(url)
        .then((resp) => {
          this.order = resp.data;
          this.isLoadingOrder = false;
        })
        .catch(() => {
          this.isLoadingOrder = false;
          this.failLoadingOrder = true;
        });
    },
  },
  mounted() {
    this.loadOrderData();
  },
};
</script>

