<template>
  <ul class="catalog__list" v-show="!isLoading && !isEmptyProductsList">
    <CatalogItem v-for="product of productsList" :product="product" :key="product.id" />
  </ul>
</template>

<script>
// eslint-disable-next-line import/no-unresolved
import CatalogItem from '@/components/CatalogItem.vue';

export default {
  name: 'CatalogList',
  components: { CatalogItem },
  props: ['products', 'loadingProducts'],
  computed: {
    isLoading() {
      return this.loadingProducts;
    },
    isEmptyProductsList() {
      return this.products.length === 0;
    },
    productsList() {
      return this.products;
    },
  },
};
</script>
