<template>
  <li class="cart__order">
    <h3>{{ productTitle }}</h3>
    <b>{{ productAmount }} ₽</b>
    <span>Артикул: {{ productCode }}</span>
  </li>
</template>

<script>
export default {
  name: 'OrderProductItem',
  props: ['item'],
  computed: {
    productTitle() {
      return this.item.product.title;
    },
    productAmount() {
      return this.item.quantity * this.item.price;
    },
    productCode() {
      return this.item.product.id;
    },
  },
};
</script>

