<template>
  <ul class="catalog__pagination pagination" v-if="this.isShowPagination">
    <li class="pagination__item">
      <a
        class="pagination__link pagination__link--arrow"
        :class="{ 'pagination__link--disabled': isFirstPage }"
        href="#"
        @click.prevent="prevPage"
        aria-label="Предыдущая страница"
      >
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left" />
        </svg>
      </a>
    </li>

    <li class="pagination__item" v-for="item of pagesList" :key="item.page">
      <a
        class="pagination__link"
        href="#"
        :class="{ 'pagination__link--current': item.current }"
        @click="choosePage(item.page)"
      >
        {{ item.page }}
      </a>
    </li>

    <li class="pagination__item">
      <a
        class="pagination__link pagination__link--arrow"
        :class="{ 'pagination__link--disabled': isLastPage }"
        href="#"
        @click.prevent="nextPage"
        aria-label="Следующая страница"
      >
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right" />
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'Pagination',
  props: ['page', 'pages', 'total'],
  emits: ['changePage'],
  methods: {
    prevPage() {
      if (this.currentPage > 1) {
        this.$emit('changePage', this.page - 1);
      }
    },
    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.$emit('changePage', this.page + 1);
      }
    },
    choosePage(page) {
      this.$emit('changePage', page);
    },
  },
  computed: {
    currentPage() {
      return this.page;
    },
    totalPages() {
      return this.pages;
    },
    totalItems() {
      return this.total;
    },
    isFirstPage() {
      return this.currentPage === 1;
    },
    isLastPage() {
      return this.currentPage === this.totalPages;
    },
    pagesList() {
      const res = [];

      // eslint-disable-next-line no-plusplus
      for (let i = 1; i <= this.totalPages; i++) {
        res.push({ page: i, current: this.currentPage === i });
      }

      return res;
    },
    isShowPagination() {
      return this.totalPages > 1;
    },
  },
};
</script>
