<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link :to="{ name: 'catalog' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <div class="content__row">
        <h1 class="content__title">
          Корзина
        </h1>
        <span class="content__info">
          {{ cartItemsCount }} товара
        </span>
      </div>
    </div>

    <section class="cart">
      <form class="cart__form form" @submit.prevent="submitFormCart">
        <div class="cart__field">
          <ul class="cart__list" v-if="!cartIsEmpty">
            <CartItem
              v-for="item of cartItems"
              :key="item.id"
              :item="item"
              @re-calc-total-amount="calcTotalAmount"
            />
          </ul>
          <div v-else>Корзина пуста</div>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ cartAmount }} ₽</span>
          </p>

          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
// eslint-disable-next-line import/extensions,import/no-unresolved
import axios from 'axios';
// eslint-disable-next-line import/extensions,import/no-unresolved
import { mapGetters, mapMutations } from 'vuex';
// eslint-disable-next-line import/extensions,import/no-unresolved
import { CONFIG } from '@/config';
// eslint-disable-next-line import/no-unresolved
import CartItem from '@/components/CartItem.vue';

export default {
  name: 'CartPage',
  components: { CartItem },
  data() {
    return {
      loadingCartData: true,
      cartAmount: 0,
    };
  },
  computed: {
    cartItemsCount() {
      return this.cartItems.length;
    },
    cartItems() {
      return this.getCart();
    },
    cartIsEmpty() {
      return this.cartItems.length === 0;
    },
  },
  methods: {
    ...mapGetters(['getUserAccessKey', 'getCart']),
    ...mapMutations(['setCart']),
    loadActualCartData() {
      const userKey = this.getUserAccessKey();
      let query = '';
      if (userKey !== null) {
        query = `?${(new URLSearchParams({ userAccessKey: userKey })).toString()}`;
      }
      const url = `${CONFIG.api.base_url}/api/baskets${query}`;
      axios.get(url)
        .then((resp) => {
          this.setCart(resp.data.items);
          this.calcTotalAmount();
        });
    },
    calcTotalAmount() {
      setTimeout(() => {
        this.cartAmount = 0;
        this.cartItems.forEach((item) => {
          this.cartAmount += item.price * item.quantity;
        });
      }, 200);
    },
    submitFormCart() {
      this.$router.push({ name: 'ordering' });
    },
  },
  mounted() {
    this.loadActualCartData();
  },
};
</script>

