<template>
  <router-link :to="{ name: 'cart' }" class="header__cart" aria-label="Корзина с товарами">
    <svg width="19" height="24">
      <use xlink:href="#icon-cart" />
    </svg>
    <span class="header__count" aria-label="Количество товаров">{{ cartProductsCount }}</span>
  </router-link>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'HeaderCart',
  methods: {
    ...mapGetters(['getCart']),
  },
  computed: {
    cartProductsCount() {
      return this.getCart().length;
    },
  },
};
</script>

