<template>
  <aside class="filter">
    <form class="filter__form form" action="#" @submit.prevent="submitFilter">
      <fieldset class="form__block">
        <legend class="form__legend">Цена</legend>
        <label class="form__label form__label--price">
          <input class="form__input" type="text" v-model="selectMinPrice" />
          <span class="form__value">От</span>
        </label>
        <label class="form__label form__label--price">
          <input class="form__input" type="text" v-model="selectMaxPrice" />
          <span class="form__value">До</span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Категория</legend>
        <label class="form__label form__label--select">
          <select
            class="form__select"
            name="category"
            v-model="selectCategory"
          >
            <option value="0">Все категории</option>
            <option
              v-for="item of categoryList"
              :value="item.id"
              :key="item.id"
            >{{ item.title }}</option>
          </select>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Материал</legend>
        <ul class="check-list">
          <li class="check-list__item" v-for="item of materialsList" :key="item.id">
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                v-model="selectMaterials"
                :value="item.id"
              >
              <span class="check-list__desc">
                {{ item.title }}
                <span>({{ item.productsCount }})</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Коллекция</legend>
        <ul class="check-list">
          <li class="check-list__item" v-for="item of collectionsList" :key="item.id">
            <label class="check-list__label">
              <input
                class="check-list__check sr-only"
                type="checkbox"
                v-model="selectCollections"
                :value="item.id"
              />
              <span class="check-list__desc">
                {{ item.title }}
                <span>({{ item.productsCount }})</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <button class="filter__submit button button--primery" type="submit">
        Применить
      </button>
      <button
        v-show="displayResetButton"
        class="filter__reset button button--second"
        type="button"
        @click="resetFilter"
      >
        Сбросить
      </button>
    </form>
  </aside>
</template>

<script>
export default {
  // eslint-disable-next-line vue/multi-word-component-names,vue/no-reserved-component-names
  name: 'Filter',
  props: [
    'productCategories',
    'productMaterials',
    'productCollections',
    'filterCollections',
    'filterCategory',
    'filterMaterials',
    'filterMaxPrice',
    'filterMinPrice',
    'showReset',
  ],
  data() {
    return {
      selectCategory: 0,
      selectMaterials: [],
      selectCollections: [],
      selectMaxPrice: 0,
      selectMinPrice: 0,
    };
  },
  computed: {
    categoryList() {
      return this.productCategories;
    },
    materialsList() {
      return this.productMaterials;
    },
    collectionsList() {
      return this.productCollections;
    },
    displayResetButton() {
      return this.showReset;
    },
  },
  methods: {
    submitFilter() {
      this.$emit(
        'reloadProductList',
        {
          filterCategory: this.selectCategory,
          filterMaterials: this.selectMaterials,
          filterCollection: this.selectCollections,
          filterMaxPrice: this.selectMaxPrice,
          filterMinPrice: this.selectMinPrice,
        },
      );
    },
    resetFilter() {
      this.selectCategory = 0;
      this.selectMaterials = [];
      this.selectCollections = [];
      this.selectMaxPrice = 0;
      this.selectMinPrice = 0;
      this.submitFilter();
    },
  },
  emits: [
    'reloadProductList',
    'update:filterCategory',
    'update:filterMaterials',
    'update:filterCollections',
    'update:filterMaxPrice',
    'update:filterMinPrice',
  ],
  created() {
    this.selectCategory = this.filterCategory;
    this.selectMaterials = this.filterMaterials;
    this.selectCollections = this.filterCollections;
    this.selectMaxPrice = this.filterMaxPrice;
    this.selectMinPrice = this.filterMinPrice;
  },
};
</script>

