<template>
  <div>
    <div class="content container loader-block" v-if="loadingCartData">
      <div class="loader-content">
        <div class="loader" />
      </div>
    </div>
    <div class="content container fail-load-cart" v-if="!loadingCartData && failLoadingCartData">
      Не удалось загрузить данные заказа.
    </div>
    <main class="content container" v-if="!loadingCartData && !failLoadingCartData">
      <div class="content__top">
        <ul class="breadcrumbs">
          <li class="breadcrumbs__item">
            <router-link :to="{ name: 'catalog' }" class="breadcrumbs__link">Каталог</router-link>
          </li>
          <li class="breadcrumbs__item">
            <router-link :to="{ name: 'cart' }" class="breadcrumbs__link">Корзина</router-link>
          </li>
          <li class="breadcrumbs__item">
            <a class="breadcrumbs__link">
              Оформление заказа
            </a>
          </li>
        </ul>

        <div class="content__row">
          <h1 class="content__title">
            Оформление заказа
          </h1>
        </div>
      </div>

      <section class="cart">
        <form class="cart__form form" @submit.prevent="submitForm">
          <div class="cart__field">
            <div class="cart__data">
              <label class="form__label">
                <input class="form__input" type="text" v-model="clientName" placeholder="Введите ваше полное имя">
                <span class="form__value">ФИО</span>
                <span class="form__error" v-if="isClientNameError">{{ clientNameErrorMessage }}</span>
              </label>

              <label class="form__label">
                <input class="form__input" type="text" v-model="clientAddress" placeholder="Введите ваш адрес">
                <span class="form__value">Адрес доставки</span>
                <span class="form__error" v-if="isClientAddressError">{{ clientAddressErrorMessage }}</span>
              </label>

              <label class="form__label">
                <input class="form__input" type="tel" v-model="clientPhone" placeholder="Введите ваш телефон">
                <span class="form__value">Телефон</span>
                <span class="form__error" v-if="isClientPhoneError">{{ clientPhoneErrorMessage}}</span>
              </label>

              <label class="form__label">
                <input class="form__input" type="email" v-model="clientEmail" placeholder="Введи ваш Email">
                <span class="form__value">Email</span>
                <span class="form__error" v-if="isClientEmailError">{{ clientEmailErrorMessage }}</span>
              </label>

              <label class="form__label">
                <textarea class="form__input form__input--area" v-model="orderComment" placeholder="Ваши пожелания" />
                <span class="form__value">Комментарий к заказу</span>
              </label>
            </div>

            <div class="cart__options">
              <h3 class="cart__title">Доставка</h3>
              <ul class="cart__options options">
                <li class="options__item" v-for="item of deliveryItems" :key="item.id">
                  <label class="options__label">
                    <input
                      class="options__radio sr-only"
                      type="radio"
                      name="delivery"
                      v-model="delivery"
                      :value="item.id"
                      :checked="item.id === delivery"
                    />
                    <span class="options__value">
                      {{ item.title }} <b>{{ item.price === 0 ? 'Бесплатно' : `${item.price} ₽` }}</b>
                    </span>
                  </label>
                </li>
              </ul>

              <h3 class="cart__title">Оплата</h3>
              <ul class="cart__options options">
                <li class="options__item" v-for="item of payItems" :key="item.id">
                  <label class="options__label">
                    <input
                      class="options__radio sr-only"
                      type="radio"
                      name="pay"
                      v-model="pay"
                      :value="item.id"
                    />
                    <span class="options__value">
                      {{ item.title }}
                    </span>
                  </label>
                </li>
              </ul>
            </div>
          </div>

          <div class="cart__block">
            <ul class="cart__orders">
              <order-product-item v-for="item of cartData" :key="item.id" :item="item" />
            </ul>

            <div class="cart__total">
              <p>Доставка: <b>{{ deliveryCostString }}</b></p>
              <p>
                Итого: <b>{{ cartItemsCount }}</b> товара на сумму <b>{{ cartTotalAmount }} ₽</b>
              </p>
            </div>

            <button
              class="cart__button button button--primery"
              type="submit"
              :class="{ loading: isFormLoading }"
              :disabled="isFormLoading"
            >
              Оформить заказ
              <div class="loader" />
            </button>
          </div>
          <div class="cart__error form__error-block" v-if="isFailSubmitForm">
            <h4>Заявка не отправлена!</h4>
            <p>
              Похоже произошла ошибка. Попробуйте отправить снова или перезагрузите страницу.
            </p>
          </div>
        </form>
      </section>
    </main>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex';
import axios from 'axios';
// eslint-disable-next-line import/extensions,import/no-unresolved
import { CONFIG } from '@/config';
// eslint-disable-next-line import/no-unresolved
import OrderProductItem from '@/components/OrderProductItem.vue';

export default {
  name: 'OrderingPage',
  components: { OrderProductItem },
  data() {
    return {
      loadingCartData: true,
      failLoadingCartData: false,
      failLoadingCartDataError: '',
      isOrdering: false,
      isFailSubmitForm: false,
      clientName: '',
      isClientNameError: false,
      clientNameErrorMessage: '',
      clientAddress: '',
      isClientAddressError: false,
      clientAddressErrorMessage: '',
      clientPhone: '',
      isClientPhoneError: false,
      clientPhoneErrorMessage: '',
      clientEmail: '',
      isClientEmailError: false,
      clientEmailErrorMessage: '',
      orderComment: '',
      isOrderingCommentError: false,
      orderingCommentErrorMessage: '',
      delivery: 0,
      isDeliveryError: false,
      deliveryErrorMessage: '',
      deliveryCost: 'бесплатно',
      pay: 0,
      isPayError: false,
      payErrorMessage: '',
      deliveryTypes: [],
      payTypes: [],
      isFormLoading: false,
    };
  },
  computed: {
    cartData() {
      return this.getCart();
    },
    cartItemsCount() {
      return this.cartData.length;
    },
    cartTotalAmount() {
      let amount = 0;
      this.cartData.forEach((item) => {
        amount += (item.quantity - 0) * (item.price - 0);
      });

      this.deliveryItems.forEach((item) => {
        if (item.id === this.delivery) {
          amount += item.price - 0;
        }
      });

      return amount;
    },
    deliveryItems() {
      return this.deliveryTypes;
    },
    payItems() {
      return this.payTypes;
    },
    deliveryCostString() {
      return this.deliveryCost;
    },
  },
  methods: {
    ...mapGetters(['getCart', 'getUserAccessKey']),
    ...mapMutations(['setCart']),
    submitForm() {
      this.isFormLoading = true;
      this.isFailSubmitForm = false;
      this.isClientNameError = false;
      this.clientNameErrorMessage = '';
      this.isClientAddressError = false;
      this.clientAddressErrorMessage = '';
      this.isClientPhoneError = false;
      this.clientPhoneErrorMessage = '';
      this.isClientEmailError = false;
      this.clientEmailErrorMessage = '';

      const order = {
        name: this.clientName,
        address: this.clientAddress,
        phone: this.clientPhone,
        email: this.clientEmail,
        deliveryTypeId: this.delivery,
        paymentTypeId: this.pay,
        comment: this.orderComment,
      };

      const userKey = this.getUserAccessKey();
      let query = '';
      if (userKey !== null) {
        query = `?${(new URLSearchParams({ userAccessKey: userKey })).toString()}`;
      }
      const url = `${CONFIG.api.base_url}/api/orders${query}`;
      axios.post(url, order)
        .then((resp) => {
          this.isFormLoading = false;
          this.updateCart();
          this.$router.push({ name: 'order-info', params: { id: resp.data.id } });
        })
        .catch((resp) => {
          this.isFormLoading = false;
          this.isFailSubmitForm = true;
          if (resp.response.data.error.request) {
            const errors = resp.response.data.error.request;
            if (errors.name) {
              this.isClientNameError = true;
              this.clientNameErrorMessage = errors.name;
            }

            if (errors.address) {
              this.isClientAddressError = true;
              this.clientAddressErrorMessage = errors.address;
            }

            if (errors.phone) {
              this.isClientPhoneError = true;
              this.clientPhoneErrorMessage = errors.phone;
            }

            if (errors.email) {
              this.isClientEmailError = true;
              this.clientEmailErrorMessage = errors.email;
            }
          }
        });
    },
    loadActualCartData() {
      const userKey = this.getUserAccessKey();
      let query = '';
      if (userKey !== null) {
        query = `?${(new URLSearchParams({ userAccessKey: userKey })).toString()}`;
      }
      const url = `${CONFIG.api.base_url}/api/baskets${query}`;
      axios.get(url)
        .then((resp) => {
          this.setCart(resp.data.items);
          this.calcTotalAmount();
          this.loadingCartData = false;
        })
        .catch(() => {
          this.loadingCartData = false;
          this.failLoadingCartData = true;
        });

      const deliveriesUrl = `${CONFIG.api.base_url}/api/deliveries`;
      axios.get(deliveriesUrl).then((resp) => {
        this.delivery = resp.data[0].id;
        this.deliveryTypes = resp.data;
      });
    },
    calcTotalAmount() {
      setTimeout(() => {
        this.cartAmount = 0;
        this.cartData.forEach((item) => {
          this.cartAmount += item.price * item.quantity;
        });
      }, 200);
    },
    loadPayByDeliveryType(id) {
      const query = `?${(new URLSearchParams({ deliveryTypeId: id })).toString()}`;
      const payUrl = `${CONFIG.api.base_url}/api/payments${query}`;
      axios.get(payUrl).then((resp) => {
        this.pay = resp.data[0].id;
        this.payTypes = resp.data;
      });
    },
    updateCart() {
      const userKey = this.getUserAccessKey();
      let query = '';
      if (userKey !== null) {
        query = `?${(new URLSearchParams({ userAccessKey: userKey })).toString()}`;
      }
      const url = `${CONFIG.api.base_url}/api/baskets${query}`;
      axios.get(url)
        .then((resp) => {
          this.setCart(resp.data.items);
        });
    },
  },
  mounted() {
    this.loadActualCartData();
  },
  watch: {
    delivery(val) {
      this.loadPayByDeliveryType(val);
      this.deliveryTypes.forEach((item) => {
        if (item.id === val) {
          const price = parseInt(item.price, 10);
          if (price === 0) {
            this.deliveryCost = 'бесплатно';
          } else {
            this.deliveryCost = `${item.price} ₽`;
          }
        }
      });
    },
  },
};
</script>

